@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
:root {
  --center-width: 800px;
  --background: #222;
  --diff-background: #151515;
  --light-background: #cbf7ea;
  --side-margin: 30px;
  --font-size: 16px;
  --line-height: 32px;
  --desc-font-size: 14px;
  --big-font-size: 18px;
}

@media (min-width: 1384px) {
  :root {
    --side-margin: 25px;
    --font-size: 12px;
    --line-height: 24px;
    --desc-font-size: 10px;
    --big-font-size: 16px;
  }
}

@media (min-width: 1730px) {
  :root {
    --side-margin: 30px;
    --font-size: 16px;
    --line-height: 32px;
    --desc-font-size: 14px;
    --big-font-size: 18px;
  }
}

* {
  font: 500 var(--font-size) Roboto;
}

p, a {
  line-height: var(--line-height) !important;
}

body {
  background: var(--background);
  color: #fff;
  margin: 0;
}

a {
  color: #fff !important;
}

a:hover {
  font-weight: 900 !important;
}

#page {
  width: 100vw;
  height: 100vh;
}

script {
  display: none !important;
}

@media (max-width: 1383px) and (min-width: 800px) {
  :root {
    --side-margin: calc(50vw - 400px);
  }

  #pre, #post {
    margin-left: var(--side-margin);
    margin-right: var(--side-margin);
    text-align: center;
    padding: 2em 0;
  }

  #pre.setup {
    text-align: justify;
  }

  #center-form {
    background: var(--light-background);
    width: 100vw;
  }

  .form-wrap {
    border-top: 2px solid #fff;
    border-bottom: 2px solid #fff;
    flex-direction: column;
    justify-content: center;
    display: flex;
    background: var(--diff-background) !important;
  }

  .form {
    width: 800px;
    left: var(--side-margin);
    right: var(--side-margin);
    grid-template-columns: auto repeat(4, 1fr);
    padding: 2em 0;
    display: grid;
    position: relative;
  }
}

@media (min-width: 1384px) {
  #center-form, #pre, #post {
    margin: auto;
    position: absolute;
    inset: 0;
  }

  #pre, #post {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  #pre {
    left: var(--side-margin);
    right: calc(50vw + 400px + var(--side-margin));
  }

  #post {
    left: calc(50vw + 400px + var(--side-margin));
    right: var(--side-margin);
  }

  #page #pre, #page #post {
    margin: var(--side-margin);
  }

  #page #center-form {
    height: calc(100vh - 8px);
    background: var(--light-background);
    width: var(--center-width);
    left: 0;
    right: 0;
  }

  .form-wrap {
    border-left: 2px solid #fff;
    border-right: 2px solid #fff;
    flex-direction: column;
    justify-content: center;
    padding: 0 2em;
    display: flex;
    background: var(--diff-background) !important;
  }

  .form {
    left: var(--side-margin);
    right: var(--side-margin);
    grid-template-columns: auto repeat(4, 1fr);
    margin: auto 0;
    display: grid;
    position: absolute;
  }
}

.form .tag {
  grid-column: 1;
  margin: 0 1em 0 0;
}

.form input {
  grid-column: 2 / 6;
}

.form .description {
  font-size: var(--desc-font-size);
  grid-column: 1 / 6;
  font-weight: normal;
  line-height: var(--font-size) !important;
}

.form .description-big {
  font-size: var(--big-font-size);
  grid-column: 1 / 6;
}

